$box-size: 100px;
$border-width: 1px;
$border-color: #000;
$background-color: #eee;
$background-hover-color: #ffbfcb;
$font-size: 30px;
$padding: 10px;
$columns: 7;
$gap: 1px;
$container-width: $columns * $box-size + ($columns - 1) * $gap;
$starting-day: sun;
$days-of-week: (
  "mon": 0,
  "tue": 1,
  "wed": 2,
  "thu": 3,
  "fri": 4,
  "sat": 5,
  "sun": 6,
);
